import React from 'react';
import { Formik, Field, Form} from "formik";
import * as Yup from "yup";

const RegisterForm = (props) => {

    const {firstName,lastName,email,password,confirmPassword,onSubmitProp}=props;

    return (
        <div >
          <Formik
          initialValues={{
          firstName : firstName,
          lastName:lastName,
          email:email,
          password:password,
          confirmPassword:confirmPassword,
        }}
        validationSchema={Yup.object().shape({
            firstName: Yup.string()
            .min(3, "Tu nombre es muy corto")
            .required ("Por favor ingresa tu nombre"),

            lastName: Yup.string()
            .min (3, "El apellido es muy corto")
            .required("Por favor ingrese el apellido correctamente"),

            email: Yup.string()
            .email("Correo no valido")
            .min(3, "Este correo electrnico es incorrecto")
            .required("Por favor, ingresa un correo electrnico vlido"),

            password: Yup.string()
            .equals([Yup.ref('confirmPassword'), null], "las contraseas no son iguales")
            .min(8, "La clave debe contener ms de 8 caractes")
            .required("Por favor ingrese una contrasea"),

            confirmPassword: Yup.string()
            .equals([Yup.ref('password'), null], "las contraseas no son iguales")
            .min(8, "La clave debe contener ms de 8 caractes")
            .required("Por favor ingrese la confirmacin de la contrasea"),
        })}

        onSubmit={(values, {setSubmitting}) =>{
            console.log(values)
            onSubmitProp(values);
        }}
        >
            {({
                values,
                errors,
                touched,
                handleSubmit,
                //isSubmitting,
                //validating,
                valid,
            }) =>{
        return (
            <div>
                <h2 className= "typography-h2" >REGISTRO</h2>
                <Form className= "contact" method= "post" onSubmit={handleSubmit}>
                    <label htmlFor="firstName" className="col-sm-2 col-form-label">Nombre</label>
                    <Field id='firstName'type="text" className="form-control" placeholder="Nombre" name='firstName'/>
                    {errors.firstName && touched.firstName && <p>{errors.firstName}</p>}

                    <label htmlFor="lastName" className="col-sm-2 col-form-label">Apellido</label>
                    <Field  id='lastName' type="text" placeholder="Apellido" className="form-control" name='lastName'/>
                    {errors.lastName && touched.lastName && <p>{errors.lastName}</p>}

                    <label htmlFor="email" className="col-form-label">Correo Electrnico</label>
                    <Field id='email' type="text" placeholder="Email" className="form-control" name='email'/>
                    {errors.email && touched.email && <p>{errors.email}</p>}


                    <label htmlFor="password" className="col-sm-2 col-form-label">Contrasea</label>
                    <Field  id='password' type="password" placeholder="Contrasea" className="form-control" name='password'/>
                    {errors.password && touched.password && <p>{errors.password}</p>}

                    <label htmlFor="confirmPassword" className="col-form-label">Confirmar Contrasea</label>
                    <Field  id='confirmPassword' type="password" placeholder="Confirmar Contrasea" className="form-control" name='confirmPassword'/>
                    {errors.confirmPassword && touched.confirmPassword && <p>{errors.confirmPassword}</p>}

                    <br />
                    <label>
                        <Field  type="checkbox" name="checkbox" />
                        Administrador Sucursales
                    </label>

                    <br />
                    <label>
                        <Field  type="checkbox" name="checkboxSuperAdmin" />
                        Administrador General
                    </label>


                    <br />
                    <button className='btn btn-sm btn-form' type="submit" disabled={Object.values(errors).length > 0}>Registrarse</button>


                </Form>
                </div>
        );
        }}
        </Formik>
        </div>
      );
    }
export default RegisterForm