import React from "react";
import { Formik, Field, Form } from "formik";
import * as Yup from "yup";

const EditForm = (props) => {

  const { name_product, descripcion, precio, stock, imagen, onSubmitProp, only_name  } =
  props;

  return (
    <div>
      <Formik
        initialValues={{
          name_product: name_product,
          descripcion: descripcion,
          precio: precio,
          stock: stock,
          imagen: imagen,
        }}
        validationSchema={Yup.object().shape({
          name_product: Yup.string()
            .min(3, "El nombre es muy corto")
            .max(30, "El nombre es muy largo")
            .required("Por favor ingresa un nombre de un producto"),
          descripcion: Yup.string()
            .min(2, "La descripcin es muy corta")
            .max(150, "La descripcin es muy larga")
            .required("Por favor ingresa un tipo"),
          precio: Yup.number().required("Por favor ingresa un precio"),
          stock: Yup.number().required("Por favor ingresa un stock"),
          imagen: Yup.string().required("Por favor ingresa una imagen"),
        })}
        onSubmit={(values, { setSubmitting }) => {
          console.log("INFORMACIN DEL PETFORMIK", values);
          onSubmitProp(values);
        }}
      >
        {({ errors, touched, handleSubmit }) => {
          return (
            <div>
              <Form className="">
                <div className="col-md-6 form-products">
                  <div className="form-group">
                    <label htmlFor="name_product">Nombre de producto:</label>
                    <Field
                      className="form-control"
                      id="name_product"
                      type="text"
                      name="name_product"
                      as= "select"
                    >
                      {only_name?.map(name =>{
                        return <option value={name}>{name}</option>
                      })}


                    </Field>
                    {errors.name_product && touched.name_product && (
                      <p> {errors.name_product} </p>
                    )}
                  </div>

                  <div className="form-group">
                    <label htmlFor="descripcion">Descripcin:</label>
                    <Field
                      className="form-control"
                      id="descripcion"
                      type="text"
                      placeholder="Ingresa una descripcin"
                      name="descripcion"
                    />
                    {errors.descripcion && touched.descripcion && (
                      <p> {errors.descripcion} </p>
                    )}
                  </div>

                  <div className="form-group">
                    <label htmlFor="precio">Precio:</label>
                    <Field
                      className="form-control"
                      id="precio"
                      description="precio"
                      placeholder="Ingresa un precio"
                      type="number"
                      name="precio"
                    />
                    {errors.precio && touched.precio && (
                      <p> {errors.precio} </p>
                    )}
                  </div>

                  <div className="form-group">
                    <label htmlFor="stock">Stock:</label>
                    <Field
                      className="form-control"
                      id="stock"
                      description="stock"
                      placeholder="Ingresa el stock de productos"
                      type="number"
                      name="stock"
                    />
                    {errors.stock && touched.stock && (
                      <p> {errors.stock} </p>
                    )}
                  </div>

                  <div className="form-group">
                    <label htmlFor="imagen">Igresa un Url de imagen</label>
                    <Field
                      className="form-control"
                      id="imagen"
                      description="imagen"
                      placeholder="Ingresa un url de imagen"
                      type="text"
                      name="imagen"
                    />
                    {errors.imagen && touched.imagen && (
                      <p> {errors.imagen} </p>
                    )}
                  </div>

                  <div className="form-group">
                    <button
                      className="btn-form"
                      type="submit"
                      /* disabled={
                        Object.values(errors).length > 0 ||
                        Object.values(touched).length === 0
                      } */
                    >

                      Enviar
                    </button>
                  </div>
                </div>
              </Form>
            </div>
          );
        }}
      </Formik>
    </div>
  );
}

export default EditForm;
